.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  padding: 40px;
  text-align: center; 
}

.project-title a {
  color: white; 
  text-decoration: none; 
  .project-title {
    margin-bottom: 20px; /* project-title 아래에 20px의 여백 추가 */
  }
  }

  .project-title {
    margin-bottom: 20px; /* project-title 아래에 20px의 여백 추가 */
  }

.skill-card-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.frontend-card,
.backend-card,
.etc-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 25px;
  margin: 10px;
  width: 150px; /* 각 카드의 폭을 150px로 설정 */
  max-width: 150px; /* 최대 폭도 150px로 설정 */
}


.frontend-title,
.backend-title,
.etc-title {
  margin: 0 0 20px 0;
  text-align: center;
}

.skill-div {
  text-align: center;
}

.skill-div > img {
  height: 50%;
}

.skill-div > p {
  height: 50%;
  font-weight: bold;
  margin: 0 0px 20px 0px;
}

body {
  font-family: "Gowun Dodum", sans-serif;
}

.project-card {
  width: 100%;
  max-width: 900px; /* 프로젝트 카드의 최대 폭 설정 */
}
