 .archiving {
  display: none; /* 초기에는 숨겨진 상태로 설정 */
  background-color: #000000;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.archiving-title {
  color: white;
  margin: 0 0 40px 0;
}

.card-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 25px;
  margin: 10px;
  width: 50%;
}

.card-title {
  margin: 0 0 20px 0;
}