.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 60px;
  background-color: black;
}

.home > h2 {
  margin: 60px 0px;
  color: white;
}

.home h3,
.home p {
  color: white;
}

.intro > h3 {
  font-family: "Gowun Dodum", sans-serif;
  margin: 0;
  color: white;
}

.lang {
  animation: fadein 2s;
  animation-iteration-count: infinite;
}

.lang.JAVASCRIPT {
  background: linear-gradient(#f7d238, #e4a425);
}
.lang.HTML {
  background: linear-gradient(#e99d80, #e65026);
}
.lang.CSS {
  background: linear-gradient(#2eaadb, #0673b0);
}
.lang.REACT {
  background: linear-gradient(#56bad2, rgb(44, 44, 44));
}
.lang.PYTHON {
  background: linear-gradient(#3572A5, #FFD43B);
}


.lang.JAVASCRIPT,
.lang.HTML,
.lang.CSS,
.lang.REACT,
.lang.PYTHON {
  color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.description {
  font-size: 25px;
}

.description > p {
  margin-bottom: 0;
}

.detail-btn-wrapper {
  height: 50px;
  margin-bottom: 50px;
}

@keyframes moving-arrow {
  from {
    margin-top: 0px;
  }
  to {
    margin-top: 15px;
  }
}

.home-card {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  padding: 0;
  margin: 0;
  animation: card-animation 2s;
  animation-iteration-count: 1;
}

@keyframes card-animation {
  from {
    transform: scale(0.8);
    opacity: 0.5;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
