.skills {
  margin: 0;
  background: #000000;
  padding: 60px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

}

.skills-title {
  margin: 0 0 40px 0;
  color: white;
  text-align: center;
}

.skill-card-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.frontend-card,
.backend-card,
.etc-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 25px;
  margin: 10px;
  width: 200px; /* 카드의 폭을 조절하세요 */
  max-width: 50%; /* 최대 폭을 조절하세요 */
}



.frontend-title,
.backend-title,
.etc-title {
  margin: 0 0 20px 0;
  text-align: center;
}

.skill-div {
  text-align: center;
}


body {
  font-family: "Gowun Dodum", sans-serif;
}

div.card {
  width: 200px; /* 카드의 너비를 조절하세요 */
  max-width: 100%; /* 최대 너비를 설정하세요 */
}

.skill-div > img {
  width: 50px; /* 이미지의 크기를 조절하세요 */
  height: auto;
}

.skill-div > p {
  font-weight: bold;
  margin: 0 0 20px; /* 텍스트의 마진을 조절하세요 */
}